<template>
    <div id="home">
      <Index/>
      <!-- <div @click="shuaxin()" class="shua">刷新</div> -->
      <router-view v-if="isRouterAlive"></router-view>
      <div id="remindWin" class="dis-n">
        <table id="remind" lay-filter="remind"></table>
      </div>
      <!-- <Footer></Footer> -->
    </div>
</template>

<script>
  import index from './index.vue';
  // import footer from '../footer.vue';
  import {orderRemind,orderManage} from '../config/axiosUtil'

    export default {
      name: "Home",
      components: {'Index' :index},
      data () {
        return {
          username:'',
          isRouterAlive: true,
          order : ''
        }
      },
      mounted () {
        this.username=this.$route.params.ac
        // console.log(this.username)
         var name = sessionStorage.getItem('username')
        //  console.log(name)
        if(name == undefined){
          this.$router.push({path:'/'})
        }
        jQuery(document).ready(function () {
            if (window.history && window.history.pushState) {
                $(window).on('popstate', function () {
　　　　　　　　　　　　　　　　// 当点击浏览器的 后退和前进按钮 时才会被触发， 
                    window.history.pushState('forward', null, ''); 
                    window.history.forward(0);
                    $(".layui-layer-close").click()
                });
            }
            window.history.pushState('forward', null, '');  //在IE中必须得有这两行
            window.history.forward(0);
            $(".layui-layer-close").click()
          });
          function forbidBackSpace(e) {
            var ev = e || window.event; //获取event对象 
            // console.log(ev)
            var obj = ev.target || ev.srcElement; //获取事件源 
            var t = obj.type || obj.getAttribute('type'); //获取事件源类型 
            //获取作为判断条件的事件类型 
            var vReadOnly = obj.readOnly;
            var vDisabled = obj.disabled;
            //处理undefined值情况 
            vReadOnly = (vReadOnly == undefined) ? false : vReadOnly;
            vDisabled = (vDisabled == undefined) ? true : vDisabled;
            //当敲Backspace键时，事件源类型为密码或单行、多行文本的， 
            //并且readOnly属性为true或disabled属性为true的，则退格键失效 
            var flag1 = ev.keyCode == 8
            // var flag2 = vReadOnly == true || vDisabled == true
            var flag2 = t == "password" || t == "text" || t == "textarea"
            //当敲Backspace键时，事件源类型非密码或单行、多行文本的，则退格键失效 
            // var flag2 = ev.keyCode == 8 && 
            //判断 
            if (flag1){
              if(!flag2){
                return false
              }
            }
        }
        //禁止后退键 作用于Firefox、Opera
        document.onkeypress = forbidBackSpace;
        //禁止后退键  作用于IE、Chrome
        document.onkeydown = forbidBackSpace;      

      },
      methods:{
        shuaxin(){
          this.isRouterAlive = false
          this.$nextTick(() => (this.isRouterAlive = true))
        },
        nowTimes() {
          // this.timeFormate(new Date());
          // console.log('123456')
          // var order = ''
          var that = this
          var uname = sessionStorage.getItem('username')
          layui.use(['table','form','layer'], function(){
            var table = layui.table,form = layui.form,layer = layui.layer;
            orderRemind().then((res) => {
              // console.log(res.data.data.length)
              // console.log(res.data.data)
              var dat = res.data.data.length;
              if(dat != 0 && dat != that.order){
                // layui.use(['table','form','layer'], function(){
                //   var table = layui.table,form = layui.form,layer = layui.layer;
                  layer.close(indexR);
                  var indexR = layer.open({
                      type:1,
                      title: '待受理工单',
                      content:$('#remindWin'),
                      area:['800px', '400px'],
                  })
                  table.render({
                    elem: '#remind'
                    ,height: 320
                    ,cellMinWidth: 140
                    ,url: that.$url+'orderRemind' //数据接口
					          ,method: 'post'
                    ,where:{username:uname}
                    ,page: true //开启分页
                    ,limit: 20
                    ,cols:[[
                      {field: 'id', title: '序号',width:80,type:'numbers',fixed:"left"}
                      ,{field: 'custName', title: '客户名称',fixed:"left",minWidth: 180,
                          templet: function(d){
                            console.log(d.orderInfo)
                            return d.orderInfo.custName
                          }
                      }
                      ,{field: 'woNumber', title: '工单号码',minWidth: 180,
                          templet: function(d){
                            console.log(d.orderInfo)
                            return d.orderInfo.woNumber
                          }
                      }
                    ]]
                  })
                // })
                sessionStorage.setItem('datS',dat)
              }else{
                if(dat != that.order){
                  sessionStorage.setItem('datS',dat)
                }else if(that.order == 0){
                  // layui.use(['table','form','layer'], function(){
                  //   var table = layui.table,form = layui.form,layer = layui.layer;
                    layer.close(indexR);
                  // })
                }
              // 
              }
              
              that.order = sessionStorage.getItem('datS')
              console.log(dat,"dat")
              console.log(that.order,"that.order")
              
          })
          .catch((error) => {
          })
          })
          this.timer = setTimeout(this.nowTimes, 10 * 1000);
        }
      },
      created() {
        var uRole = sessionStorage.getItem('userRole')
        if(uRole == '总部客服' || uRole == '运维助理'){
          this.nowTimes();
        }
        
      },
      beforeDestroy: function() {
        if (this.timer) {
            console.log("销毁定时器")
          clearTimeout(this.timer); //在Vue实例销毁前，清除我们的定时器
        }
      }
    }
</script>

<style scoped>
#home .shua{
    text-align: center;
    margin-left: 457px;
    position: absolute;
    z-index: 99999;
}
</style>



